.contact-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem 2rem;
  background-color: white; 
  color: #333;
  text-align: center;
  font-family: 'Arial', sans-serif;
}

.contact-container h2 {
  font-size: 2rem;
  color: #1a3a6d; 
  margin-bottom: 1rem;
  font-weight: bold;
}

.intro-text {
  font-size: 1rem;
  color: #555;
  margin-bottom: 1.5rem;
  line-height: 1.6;
  max-width: 750px;
  margin-left: auto;
  margin-right: auto;
}

.contact-info {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin-top: 2rem;
}

.contact-card {
  background-color: #ffffff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  border-left: 4px solid #0c4ca6; 
  transition: transform 0.3s ease;
}

.contact-card h3 {
  font-size: 1.2rem;
  color: #1a3a6d; 
  margin-bottom: 0.8rem;
}

.contact-card p {
  font-size: 1rem;
  color: #555;
}

.contact-card a {
  color: #0c4ca6;
  text-decoration: none;
  font-weight: 600;
}

.contact-card a:hover {
  color: #005fa3; 
}

.contact-card:hover {
  transform: translateY(-5px); 
}


@media (min-width: 600px) {
  .contact-info {
    grid-template-columns: repeat(2, 1fr); 
  }
}
